import {useState,Fragment} from 'react';
import quest from './data.json';
function handleClick(){
    const [exp,setExp] = useState(false);
    function onClick(){
        setExp(!exp);
    }
    return {
        exp,
        onClick
    }
}
function QuestItem({title,content,id,...ortherProps}){
    const state = handleClick();
    return (
        <div className={`py-3 ${id===quest.length-1?'':'border-b border-grey-light'}`}>
            <p className={`text-2xl mb-2 cursor-pointer relative ${state.exp?'text-grey-darkest':'text-blue'}`} onClick={state.onClick}>{title || 'title'}
                {state.exp?(<span className="hidden md:flex absolute text-grey-darkest inset-y-0 right-0 items-center">
                                        <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="minus" className="text-grey-dark fill-current w-4 h-4" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><path fill="currentColor" d="M416 208H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h384c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z"></path></svg>
                                    </span>):(<span className="hidden md:flex absolute text-grey-darkest inset-y-0 right-0 items-center">
                    <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="minus" className="text-blue-light fill-current w-4 h-4" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512">
                    <path fill="currentColor" d="M416 208H272V64c0-17.67-14.33-32-32-32h-32c-17.67 0-32 14.33-32 32v144H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h144v144c0 17.67 14.33 32 32 32h32c17.67 0 32-14.33 32-32V304h144c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z"></path></svg>
                </span>)}
            </p>
            <div className={`text-lg text-blue-darker leading-normal ${state.exp?'':'hidden'}`}>{content.map((val,key)=><p key={key}>{val}</p>)}</div>
        </div>
    )
}
function Quest(){
    return (
        <Fragment>
            {quest.map((val,key)=><QuestItem title={val.title} content={val.content} key={key} id={key}/>)}
        </Fragment>
    )
}
export default Quest;